/******************************************************************

Stylesheet: Form Styles

******************************************************************/

/*********************
INPUTS
*********************/

// setting up defaults
input[type="text"],
input[type="password"],
input[type="datetime"],
input[type="datetime-local"],
input[type="date"],
input[type="month"],
input[type="time"],
input[type="week"],
input[type="number"],
input[type="email"],
input[type="url"],
input[type="search"],
input[type="tel"],
input[type="color"],
select,
textarea,
.field {
  display: block;
  height: 40px;
  line-height: 1em;
  padding: 0 12px;
  margin-bottom: 14px;
  font-size: 1em;
  border-radius: 2px;
  vertical-align: middle;
  box-shadow: none;
  border: 0;
  width: 100%;
  background-color: $white;
  border: 1px solid $black;
  @include transition(background-color 0.24s ease-in-out);

  // a focused input (or hovered on)
  &:focus,
  &:active {
    background-color: darken($white, 3%);
    border: 1px solid lighten($black, 10%);
    outline: none; 
  } // end hover or focus

  // error notification
  &.error,
  &.is-invalid {

    background-color: $white;
    background-position: 99% center;
    background-repeat: no-repeat;
    background-image: url(data:image/png;base64,);

  } // end invalid input

  // success notification
  &.success,
  &.is-valid {

    background-color: $white;
    background-position: 99% center;
    background-repeat: no-repeat;
    background-image: url(data:image/png;base64,);

  } // end valid input

  // a disabled input
  &[disabled],
  &.is-disabled {
    cursor: not-allowed;

    opacity: 0.6;

    &:focus,
    &:active {

    } // end disabled focus

  } // end disabled input

} // end input defaults

// spacing out the password (thx @murtaugh)
input[type="password"] {
  letter-spacing: 0.3em;
}

// textareas
textarea {
  max-width: 100%;
  min-height: 120px;
  line-height: 1.5em;
  padding: 12px;
}

// selects
select {
  -webkit-appearance: none; /* 1 */
  -moz-appearance: none;
  appearance: none;
  // Base64 encoded little arrow
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: 97.5% center;
}

input[type="submit"]{

padding: 7px 20px 9px; 
background:#222;
color: white;
border: none;

}

input[type="submit"]:hover{

background:#444;
color: white;

}